@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700;1,300&family=Poppins:wght@300;400;700&display=swap);
.main-header {
    height: 50px;
    background: var(--color-primary-dark);
    color: var(--color-text-light);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
    opacity: .95;
}

.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1440px;
    margin: auto;
    height: 100%;
    padding-left: 2rem;
    padding-right: 2rem;
}

.header-container a {
    color: var(--color-text-light);
    text-decoration: none;
}

.left-nav a {
    padding: 3rem;
    padding-left: 0;
}

.left-nav a:hover {
    cursor: pointer;
    color: var(--color-primary-pink);
}
.right-nav a {
    padding: 1rem;
    padding-right: 0;
}

.right-nav a:hover {
    cursor: pointer;
    color: var(--color-primary-pink);
}
.hero-container {
    min-height: 100vh;
    background: linear-gradient(0deg, rgba(117, 53, 64, .75), rgba(234, 192, 192, .75)), url(/static/media/family-beach1440.7abb4376.jpg)  center center/cover;
    position: relative
}

.hero-title {
    position: absolute;
    left: 50%;
    top: 30%;
    transform: translate(-50%, -50%)
}

.hero-katrinas {
    font-size: 14rem;
    z-index: 1;
    color: var(--color-primary-dark)
}
.hero-creations {
    position: absolute;
    font-size: 8rem;
    left: 40%;
    top: 50%;
    z-index: 2;
    color: var(--color-primary-pink)
}

.hero-cta {
    color: #ffffff;
    position:absolute;
    top: 65%;
    left: 50%;
    font-size: 1.5rem;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
}

.hero-cta-button {
    text-decoration: none;
    color: var(--color-primary-dark);
    background: var(--color-primary-pink);
    border-radius: 50px;
    padding: 1.2rem;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    border: 2px solid var(--color-primary-dark);
    margin: auto;
    margin-top: 1rem;
    font-weight: 700;
    font-size: 1.2rem;
    box-shadow: 1px 5px 15px 5px rgba(0, 0, 0, .15);
}

.hero-cta-button:hover {
    cursor: pointer;
}

.hero-cta-button:focus {
    outline: none;
}

.hero-cta-button:active {
    box-shadow: 1px 5px 15px 5px rgba(0, 0, 0, .1);
}
.section-collections {
    width: 100%;
}

.collections-container {
    max-width: 1440px;
    padding: 2rem;
    position: relative;
    margin: auto;
}

.collections-h2 {
    margin-top: 2rem;
    margin-bottom: 4rem;
    color: var(--color-primary-dark);
    font-family: var(--font-heading-primary);
    text-align: center;
    font-size: 1.75rem;
}

.section-collection {
    position: absolute;
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 100%;
    margin: auto;
    top: 0;
}

.collections-bg {
    position: relative;
    width: 100%;
    height: 70vh;
}

.collections-bg-top {
    background-color: #FFFFFF;
    height: 60%;
}

.collections-bg-bottom {
    background-color: var(--color-primary-pink);
    height: 40%;
}
.collection-item {
    flex: 1 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    max-width: 350px;
}

.collection-item-img {
    height: 70%;
    border: 1px solid var(--color-primary-dark);
    transition: all .5s ease;
    z-index: 2;
}

.collection-item-img:hover {
    transform: scale(1.025) translateZ(0);
    cursor: pointer;
}

.collection-item-text {
    height: 30%;
}

.collection-item-text {
    padding: 1rem .5rem 1rem .5rem;
}

.collection-item-text h3 {
    color: var(--color-primary-dark);
    font-family: var(--font-heading-primary);
    text-align: center;
    font-size: 1.5rem;
    margin-top: 1rem;
}

.collection-item-text p {
    color: var(--color-primary-dark);
    text-align: center;
    margin-top: 1rem;
}
:root {
    --color-primary-dark: #753540;
    --color-secondary-dark: #75585D;
    --color-secondary-dark2: #C25769;
    --color-primary-pink: #F7B9C3;
    --color-secondary-pink: #F56E84;
    --color-text-light: #ffffff;
    --font-heading-primary: 'Poppins', sans-serif;
    --font-text-primary: 'Open Sans', sans-serif;
    --font-heading-size: 1.5rem;
    --font-paragraph-size: 1rem;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-family: var(--font-text-primary);
    font-size: 1rem;
    font-size: var(--font-paragraph-size);
    overflow-x: hidden;
}
